const draw = (ctx) => {
  clearLocalCanvas(ctx);
};

const clearLocalCanvas = (ctx) => {
  //console.log("Clear Canvas called");
  ctx.fillStyle = "green";
  ctx.fillRect(0, 0, canvas.width, canvas.height);
};

