const resizeCanvas = (canvas, newWidth, newHeight) => {
  canvas.width = newWidth;
  canvas.height = newHeight;
  width = newWidth;
  height = newHeight;
};

//
export const Canvas = () => {
  const classes = useStyles();
  const canvasRef = useRef();

  disableBodyScroll(canvasRef);
  window.addEventListener("resize", resizeCanvas, false);

  //
  const handleCanvasDown = async (canvas, evt) => {};

  //
  const handleCanvasUp = (canvas, evt) => {};

  //
  const handleCanvasMove = async (canvas, evt) => {};

  //
  const handleCanvasKeyPress = (canvas, evt) => {
    console.log(evt);

    switch (evt) {
      case "":
        break;

      case "":
        break;

      case "":
        break;

      case "":
        break;
    }

    var action = "";
    var actionInput = "";

    postAction(action, actionInput);
  };

  //
  useEffect(async () => {
    init();

    canvas = canvasRef.current;

    let requestId;

    //render/update method, called on each key frame
    const render = async () => {
      const width = window.innerWidth - 15;
      const height = window.innerHeight - 160;
      // resizeCanvas(canvas, width, height);

      const ctx = canvas.getContext("2d");
      draw(ctx);
      requestId = requestAnimationFrame(render);
    };

    await render();
    return () => {
      cancelAnimationFrame(requestId);
    };
  });

  //
  return (
    <div>
      <canvas
        tabindex="1"
        ref={canvasRef}
        width={width}
        height={height}
        onKeyDown={(evt) => handleCanvasKeyPress(canvas, evt)}
        onMouseDown={(evt) => handleCanvasKeyPress(canvas, evt)}
      />
    </div>
  );
};
